<template>
    <div class="user">
        <template>
            <el-table
            :data="userList"
            style="width: 100%">
                <el-table-column
                    label="角色">
                    <template slot-scope="scope">
                        <span v-if="scope.row.admin_post == 1">经理</span>
                        <span v-if="scope.row.admin_post == 2">客服</span>
                        <span v-if="scope.row.admin_post == 3">员工</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="admin_name"
                    label="姓名">
                </el-table-column>
                <el-table-column
                    prop="admin_id"
                    label="账号">
                </el-table-column>
                <el-table-column
                    prop="admin_psw"
                    label="密码" v-if="roles == 'admin' || roles == 'super_editor'">
                </el-table-column>
                <el-table-column
                    prop="admin_phone"
                    label="手机号码">
                </el-table-column>
                <el-table-column
                    label="权限">
                    <template slot-scope="scope">
                        <span v-if="scope.row.admin_jurisdiction == 'super_editor'">超级管理员</span>
                        <span v-if="scope.row.admin_jurisdiction == 'admin'">管理员</span>
                        <span v-if="scope.row.admin_jurisdiction == 'staff'">访问人</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" v-if="roles == 'admin' || roles == 'super_editor'">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row.admin_uid)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row.admin_uid)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>


        <el-dialog title="修改信息" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="姓名" :label-width="formLabelWidth">
                    <el-input v-model="form.admin_name" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="账号" :label-width="formLabelWidth">
                    <el-input v-model="form.admin_id" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                    <el-input v-model="form.admin_psw" placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item label="手机号码" :label-width="formLabelWidth">
                    <el-input v-model="form.admin_phone" placeholder="请输入手机号码"></el-input>
                </el-form-item>
                <el-form-item label="职位" :label-width="formLabelWidth">
                    <el-select v-model="form.admin_post" placeholder="请选择职位">
                        <el-option label="经理" value="1"></el-option>
                        <el-option label="客服" value="2"></el-option>
                        <el-option label="员工" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="权限" :label-width="formLabelWidth">
                    <el-select v-model="form.admin_jurisdiction" placeholder="请选择权限">
                        <el-option label="超级管理员" value="super_editor"></el-option>
                        <el-option label="管理员" value="admin"></el-option>
                        <el-option label="访问人" value="staff"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleUpdate">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                userList:[],
                roles:"",
                dialogFormVisible: false,
                form: {
                    uid:"",
                    admin_name: '',
                    admin_id:"",
                    admin_jurisdiction:"",
                    admin_phone:"",
                    admin_post:"",
                    admin_psw:"",
                    admin_type:""
                },
                formLabelWidth: '120px'
            }
        },
        created() {
            this.getUser(); // 获取用户
            this.roles = this.$store.state.userInfo.roles;
        },
        methods: {
            getUser () {
                let that = this;
                let url = '/api'+that.$common.getAllUser;
                this.$http.post(url).then((res)=>{
                    if ( res.code == '200' ) {
                        that.userList = res.data;
                        that.form.admin_name = res.data[0].admin_name;
                        that.form.admin_id = res.data[0].admin_id;
                        that.form.admin_jurisdiction = res.data[0].admin_jurisdiction;
                        that.form.admin_phone = res.data[0].admin_phone;
                        that.form.admin_post = res.data[0].admin_post;
                        that.form.admin_psw = res.data[0].admin_psw;
                        that.form.admin_type = res.data[0].admin_type;
                    } else if( res.code == '404' ){
                        sessionStorage.clear();
                        this.$router.push({ path:'/login' });
                    }
                }).catch(data=>{
                    this.$message({
                        type: 'success',
                        message: '获取失败!'
                    });
                })
            },
            handleUpdate () {
                let that = this;
                let data = {
                    uid:that.form.uid,
                    admin_name: that.form.admin_name,
                    admin_id:that.form.admin_id,
                    admin_jurisdiction:that.form.admin_jurisdiction,
                    admin_phone:that.form.admin_phone,
                    admin_post:that.form.admin_post,
                    admin_psw:that.form.admin_psw,
                }
                let url = '/api'+that.$common.updateUser;
                this.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if (res.status == 1) {
                        this.dialogFormVisible = false;
                        this.getUser(); // 获取用户
                        this.$message({
                            type: 'success',
                            message: '修改成功!'
                        });
                    }else {
                        this.$message({
                            type: 'success',
                            message: '已取消修改!'
                        });
                    }
                }).catch(data=>{
                    this.$message({
                        type: 'success',
                        message: '修改失败!'
                    });
                })
            },
            handleEdit (index,uid) {
                this.dialogFormVisible = true;
                this.form.uid = uid;
            },
            handleDelete (index,uid) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let that = this;
                    let data = {
                        uid:uid
                    }
                    let url = '/api'+that.$common.deleteUser;
                    this.$http.post(url,data).then((res)=>{
                        if (res.status == 1) {
                            this.getUser(); // 获取用户
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                        }else {
                            this.$message({
                                type: 'success',
                                message: '已取消删除!'
                            });
                        }
                    }).catch(data=>{
                        this.$message({
                            type: 'success',
                            message: '删除失败!'
                        });
                    })
                    
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除!'
                    });          
                });
            }
        },
        mounted() {
            
        },
    }
</script>

<style lang="scss" scoped>

</style>



